.projects{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project{
    width: 60vw;
    height: 22.5vw;
    transition: all ease-in-out 0.15s;
    filter: drop-shadow(2px 10px 5px #00000015);

}

.project-container:hover{
    transform: translateY(-5%);
    transition: all ease-in-out 0.25s;
    filter: drop-shadow(2px 10px 8px #00000025);
    cursor: pointer;
}

.project-info{
    margin-top: 0;
    width: 10vw;
    height: 1.5vw;
}

.tech-stack{
    width: 25vw;
    height: 2rem; 
}
.project-title-mobile{
    display: none;
}


@media only screen and (max-width: 600px){
    .projects{
        gap: 1rem;
    }

    .project{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0.25rem;
        height: auto;
        width: 80%;
    }
    .project-container{
            width: 100vw;
            height: auto;
            display: flex;
            align-items: center;
            justify-content: center;
    }

    .project > div{
        width: 80%!important;
        height: auto!important;
    }
    .project > img{
    
        width: 100%;
    }

    .project p {
        width: 100%!important;
        font-size: 2.5vw!important;
        height: auto;
        padding: 1rem;
    }
    .project-title{
        display: none;
    }
    .project-title-mobile{
        display: block;
        font-size: 4vw!important;
        padding: 0;
        padding-left: 4rem;
        text-align: left;
        margin: 0;
        width: 100%;
    }

    .tech-stack{
        margin: 0.5rem;
        width: auto;
    }
    .tech-stack h1{
        font-size: 0.75rem;
    }

    .project-info{
        margin-top: 0.5rem;
    }

    .project-info img{
        width: 2.5vw;
    }
}