*{
  font-family: Circe;
  margin: 0;
}
.nav-bar{
  display: flex;
  align-items: center;
  gap: 2rem;
  position: fixed;
  top: 0;
  background-color: rgba(255, 255, 255, 0.678);
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  height: 3rem;
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, .08235294117647059);
  z-index: 1000;
  box-shadow: 0 5px 8px rgba(0, 0, 0, .08235294117647059);
}

.navicons {
  position: fixed;
  width: 2.5vw;
  top: 65vh;
  left: 1vw;
  z-index: 3;
  display: flex;
  flex-direction: column;

}
.navicons img {
  margin-bottom: 0.5vw;
  width: 100%;
  transition: all 0.2s ease-in-out;
  opacity: 50%;
  cursor: pointer;
}

.logo{
  flex: 0.98;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-left: 0.5rem;
}

.logo img{
  width: 2.5rem;
  transition: all 0.2s ease-in-out;
}

.logo img:hover {
  transform: scale(1.1);
  transition: all 0.3s ease-in-out;
}

.link {
  opacity: 80%;
  height: 1.25rem;
  width: 1.25rem;
  transition: all 0.2s ease-in-out;
  z-index: 4;
  /*box-shadow: 10px 10px 10px #00000030;*/
}
.link:hover {
  opacity: 100%;
  transform: scale(1.15);
  transform: translate(0, -5px);
  transition: all 0.3s ease-in-out;
  cursor: pointer;

}

.link-image{
  width: 100%;
}

/*hero*/
.hero{
  height: calc(100vh);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-bottom: 5vh;
}

.info{
  width: 50%;
  min-width: 50rem;
  margin-top: -12rem;
  margin-left: -10rem;
  color: #2a2a2a;
}

.name-title{
  font-size: 6rem;
  padding: 0;
  margin: 0;
  line-height: 5rem;
  text-align: left;
  color: #363636;
}

.blink{
  opacity: 0.6;
  margin: 0;
  padding: 0;
  margin-bottom: -1.25rem;
  height: 2.5rem;
  width: 4rem;
}

.name-grey{
  color: #c7c7c7;
  font-weight: lighter;
}

.title-tags{
  font-weight: lighter;
  font-size: 5.5rem;
}
.titles{
  width: 100%;
}
.titles > *{
  margin-top: 0.5rem;
  margin-bottom: 0.75rem;
  display: flex;
  height: 2rem;
  justify-content: flex-start;
  align-items: center;
  gap: 1.25rem;
  align-items: center;
  padding: 0;
  font-size: 1.25rem;
  font-weight: bold;
  margin: 0;
}

.titles img{
  width: 1.75rem;
}
.main-green{
 color: #3fb66c;
}

.info-paragraph{
  margin-top: 0.5rem;
  line-height: 1.5;
  padding: 0;
  color:#868181;
  font-size: 1.25rem;
  font-weight: lighter;
}

.sectiontitle hr{
  height: 1px;
  width: 30%;
  opacity: 30%;
}
.sectiontitle{
  margin: 0 auto;
  margin-top: 2.5vw;
  margin-bottom: 2.5vw;

  display: flex;
  justify-content: center;
  align-items: center;
}
.sectiontitle h2{
  width: auto!important;
  line-height: 0%;
  font-size: 2.5vw;

}
.sectiontitle img{
  display: inline-block;
  margin-right: 1vw;
  width: 2.5vw;
  margin-left: 0.5rem;
}

/* hero art work*/
.mahad-1{
  position: absolute;
  height: 40rem;
  left: 67%;
  bottom: 15%;
}

/*tablet*/
@media only screen and (max-width: 1000px){

  .hero{
    justify-content: flex-start;
    align-items: center;
  }
  .info{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 0;
    margin-top: 10rem;
    margin-left: 0;
    padding: 1rem;
    color: #2a2a2a;
  }

  .navicons{
    width: 3rem;
    top: 80vh;
  }

  .info-paragraph{
    width: 90%;

  }

  .name-title, .title-tags, .name-grey{
    font-size: 4rem;
  }

  .titles *{
    font-size: 1rem;
    justify-content: center;
    gap: 1%;
  }
  .mahad-1{
    position: static;
    height: 60%;
  }
}

/*mobile*/
@media only screen and (max-width: 600px) {
  /* html{
    overflow-x: hidden;
  } */
  body{
    max-width: 100%;
    margin: 0;
    padding: 0;
    /* overflow-x: hidden; */
  }

  .navicons{
    display: none;
  }

  .nav-bar{
    width: 100vw;
  }
  

  
  .logo{
    flex: 0.85;

  }

  .hero{
    width: 100vw;
    height: 100vh;
    display: flex;
    margin-bottom: 20vh;
  }

  .info{
    width: 90%;
    max-width: 100%;
    height: 30%;
    min-width: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    color: #2a2a2a;
  }

  .info-paragraph{
    width: 100%;
  }
  .main-green{
    display: none!important;
  }

  .name-title{
    margin-bottom: 0!important;
  }

  .name-title, .name-grey, .title-tags{
    width: 100%;
    font-size: 10vw;
  }

  .titles > div p{
    text-wrap: nowrap;
  }
  
  .titles *{

    color: #3fb66c;
    margin-top: 0.5rem;
    margin-bottom: 0.75rem;
    display: flex;
    height: 2rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    align-items: center;
    padding: 0;
    font-size: 3.65vw!important;
    font-weight: bold;
    margin: 0;
  }

  .test{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 10vh;
    z-index: 999;
  }


  .blink{
    width: 10vw;
  }

  .sectiontitle h2{
    font-size: 3.5vw;
  }

  .sectiontitle hr{
    margin: 0;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
}