.portfoliosection{
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

@media only screen and (max-width: 600px){
    .portfoliosection{
        margin: none;
        width: 80%;
        display: flex;
        gap: 0.5rem;
        flex-direction: column;
        align-items: center;
    }

    .portfolio{
        width: 100%!important;
        height: auto!important;
    }

    .portfolio img{
        width: 100%!important;
    }

    .portfolio div{
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
    }
    .portfolio div h1{
        font-size: 4vw!important;
    }

    .portfolio div p{
        font-size: 3vw;
    }
}