.get-in-touch{
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}

.get-in-touch-title{
    font-size: 5rem;
    font-weight: 900!important;
}

h2{
    color: #2e2e2e
}

@media only screen and (max-width: 1000px){
    .get-in-touch-title, .get-in-touch-title *{
        font-size: 8vw;
    }
}

@media only screen and (max-width: 600px){
    hr{
        margin-top: 6vh;
    }
    .get-in-touch{
        width: 80%;
    }
    .get-in-touch-title, .get-in-touch-title *{
        font-size: 8vw;
    }

    .get-in-touch p{
        font-size: 3vw!important;
    }
}