.work-experience{
    display: flex;
    width: 85%;
    border-top: 1px solid rgb(206, 206, 206);
    margin: 0 auto;
}

.experience-info{

}

.experience-company{
    color: #3fb66c;
    margin-left: 0.5rem;
    font-size: 1.5rem;
}

.experience-location{
    opacity: 0.5;
    margin: 0.25rem;
}

.experience-year{
    font-style: normal;
    margin-left: 0.4rem;
    opacity: 0.5;
}
.experience-info img{
    max-height: 30vh;
}

.experience-description{
    padding: 1rem;
    color: white;
    background-color: rgba(0, 0, 0, .7);
    border-radius: 2px;
    filter: drop-shadow(2px 10px 5px #00000030);
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
}

@media only screen and (max-width: 1000px){
    .work-experience{
        width: 100%;
    }
}